.contact__container {
    display: grid;
    place-content: center;
}

.contact__wrapper {
    width: fit-content;
    display: flex;
    gap: 2rem; 
}

.contact__wrapper a {
    background:var(--color-primary);
    font-size: 2rem;
    border: 2px solid transparent;
    transition: var(--transition);
    padding: 2rem;
    border-radius:1.5rem ;
}

.contact__wrapper a:hover {
    background: transparent;
    border-color:var(--color-gay-100) ;
}






/* MEDIA QUEIES (medium screen)*/ 
@media screen and (max-width: 1024px){
.contact__wrapper {
    gap: 1.5rem;
}

.contact__wrapper a {
    padding: 1rem;
    font-size: 1.2rem;
    border-radius: 1rem;
}
}