.plan__container {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 3rem;
}

.plan h3 {
    margin-bottom: 0.8rem;
}

.plan small {
    margin-bottom: 2rem;
}

.plan h1 {
    color: var(--color-primary-vairant);
    display: inline;
}

.plan h2 {
    display: inline;
    color: var(--color-gray-400);
}

.plan h4 {
    margin-bottom: 2rem;
}

.plan p {
    color: var(--color-gray-100);
}
.plan p.disabled {
    color: var(--color-gray-400);
}

.plan button {
    margin-top: 3rem;
    cursor: pointer;
}


/* MEDIA QUEIES (medium screen)*/ 
@media screen and (max-width: 1024px){
.plan__container {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
    }
    
    
    /* MEDIA QUEIES (small screen)*/ 
    @media screen and (max-width: 600px){
.plan__container {
    grid-template-columns: 1fr;
}    
    .plan {
        width: 90%;
        margin-inline: auto;
    }
    
    }